.Container {
    display: flex;
    -webkit-display: flex;
    width: 100%;
    /* height: 100vh; */
    flex-direction: row;
    -webkit-flex-direction: row;
    flex-wrap: wrap;
    -webkit-flex-wrap: wrap;
    /* justify-content: center; */
    /* -webkit-justify-content: center; */
    /* flex-basis: 25%; */
}

.WorkoutCard {
    background: white;
    font-family: 'Open Sans', sans-serif;
    border-radius: 3px;
    box-shadow: 0 5px 3px rgba(0,0,0,0.3), 0 1px 2px rgba(0,0,0,0.24);
    padding-bottom: 20px;
    margin: 10px;
    /* box-sizing: border-box;
    flex: 1, 0, 15%; */
    /* max-height: 50%; */
}

.WorkoutCard:active {
    box-shadow: 0 1px 1px rgba(0,0,0,0.3), 0 1px 2px rgba(0,0,0,0.24);
}

/* .WorkoutImage {
    width: 20%;
    max-width: 20%;
} */
.Title {
    display: flex;
    justify-content: space-between;
    text-align: left;
    align-items: flex-start;
    /* padding-left: 5%; */
    padding-top: 5%;
    width: 90%;
    padding-left: 5%;
}

.TitleText {
    margin: '0';
    max-width: 300px;
    overflow-wrap: normal;
}

.TimeDistance {
    font-weight: bold;
    font-size: 15px;
    display: flex;
    padding-left: 5%;
}

.Item {
    padding: 5px;
    display: flex;
    align-items: center;
}

.Image {
    position: relative;
}
.Playlist {
    position: absolute;
    top: 25%;
    right: 10%;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Backdrop {
    background: rgba(39, 46, 63, 0.5);
    position: absolute;
    top: 0;
    right: 0;
    width: 40%;
    height: 75%;
}

.Number {
    font-weight: 900;
    font-size: 30px;
    margin: 5px;
    text-align: center;
}

.WorkoutsText {
    margin: 0;
    font-weight: 500;
}

.Link {
    color: rgb(0, 105, 210);
    font-weight: bold;
    font-size: 17px;
    padding-left: 5%;
}